import React, { useEffect, useState } from 'react';
import { Button, Col, Container, Row, Table } from 'reactstrap';
import { Food } from '../Food/Food';

export const Meal = ({ title, editable, foods, units }) => {
  const [_foods, setFoods] = useState(foods);
  const [totalCarb, setTotalCarb] = useState(0);
  const [totalProt, setTotalProt] = useState(0);
  const [totalFat, setTotalFat] = useState(0);
  const [_units, setUnits] = useState(units);
  const [isEditable, setIsEditable] = useState(editable);

  useEffect(() => {
    setIsEditable(editable);
  }, [editable]);

  const agg = () => {
    _foods.map(food => 4*(food.prot + food.carb) + 9*food.fat ).reduce()
  }
  const handleOnSave = () => {
    setIsEditable(false);
  };

  const handleOnAdd = () => {
    setFoods([..._foods, { id: _foods.length + 1, prot: 0, carb: 0, fat: 0 }]);
  };

  const handleOnDelete = () => {
    setFoods(_foods.filter(food => food.id !== _foods.length));
  };

  return (
    <Container>
      <Row>
        <h2>{title}</h2>
      </Row>
      <Row>
        <Table>
          <thead>
            <tr>
              <th>#</th>
              <th>name</th>
              <th>group</th>
              <th>portion</th>
              <th>calories</th>
              <th>proteins</th>
              <th>carbohydrates</th>
              <th>fat</th>
            </tr>
          </thead>
          <tbody>
            {_foods.map(item => (
              <Food
                units={item.units}
                key={item.id}
                id={item.id}
                name={item.name}
                group={item.group}
                portion={item.portion}
                carb={item.carb}
                prot={item.prot}
                fat={item.fat}
                editable={isEditable}
              ></Food>
            ))}
          </tbody>
        </Table>
      </Row>
      <Row>
        <Col xs={8}>
          <Button color="success" onClick={handleOnSave}>
            Save
          </Button>
        </Col>
        <Col xs={4}>
          <Button color="danger" onClick={handleOnDelete}>
            -
          </Button>
          <Button color="primary" onClick={handleOnAdd}>
            +
          </Button>
        </Col>
      </Row>
    </Container>
  );
};
